import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListGrnComponent } from './components/list-grn/list-grn.component';
import { FormGrnComponent } from './components/form-grn/form-grn.component';
import { RouterModule, Routes } from '@angular/router';
import { ContentHeaderModule } from 'app/layout/components/content-header/content-header.module';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoreCommonModule } from '@core/common.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { SweetalertComponent } from 'app/shared/sweetalert/sweetalert.component';
import { Ng2FlatpickrModule } from 'ng2-flatpickr';

const routes: Routes = [
  {
    path: "",
    component: ListGrnComponent,
    data: { animation: "grn-form" },
  },
   {
    path: "list",
    component: ListGrnComponent,
    data: { animation: "grn-form-list" },
  },
  {
    path: "form",
    component: FormGrnComponent,
    data: { animation: "grn-form" },
  },
  {
    path: "form/:id",
    component: FormGrnComponent,
    data: { animation: "grn-form-id" },
  },
]


@NgModule({
  declarations: [
    ListGrnComponent,
    FormGrnComponent
  ],
  imports: [
    CommonModule,
    ContentHeaderModule,
    RouterModule.forChild(routes),
    FormsModule,
    CoreCommonModule,
    NgbModule,
    NgSelectModule,
    ReactiveFormsModule, 
  ],
  providers: [SweetalertComponent],
})
export class GrnModule { }
