import { Injectable } from "@angular/core";
import { BehaviorSubject, Subject } from "rxjs";
import { numberToWords } from "./../number-to-words";
import { reportViewInHtmlReport } from "./reportViewInHtml.service";
@Injectable({
  providedIn: "root",
})
export class goodsReceivingNoteReport {
  constructor(private _reportView: reportViewInHtmlReport) { }

  generatePdfContent(getDetail) {
    let totalQuantity = 0;
    let totalAmount = 0;
    const makePdf = `
      <div class="card invoice-preview-card">
      <div class="divider">
      <div class="divider-text"><h3>GOODS RECEIVING NOTE (FABRIC)</h3></div>
    </div>
        <div class="card-body invoice-padding py-1">
          <div class="d-flex justify-content-between">
            <div>
              <div class="invoice-date-wrapper mb-0">
              <p class="invoice-date">GRN Number: </p>
              <h5 class="invoice-date-title ml-25">#NWS-00${getDetail.id}</h5>
            </div>
            </div>
            <div>
              <div class="invoice-date-wrapper mb-0">
                <p class="invoice-date">Transaction Date:</p>
                <p class="invoice-date-title ml-25">${getDetail.transactionDate
      }</p>
              </div>
              <div class="invoice-date-wrapper">
                <p class="invoice-date">Receiving Date:</p>
                <p class="invoice-date-title ml-25">${getDetail.receivingDate
      }</p>
              </div>
            </div>
          </div>
        </div>
        <hr class="m-0" />
        <div class="card-body invoice-padding pt-0">
          <div class="row invoice-spacing">
            <div class="col-6 p-0">
              <table>
                <tbody>
                  <tr>
                    <td class="pr-1">Customer Name:</td>
                    <td>${getDetail.supplierName}</td>
                  </tr>
                  <tr>
                    <td class="pr-1">Person Name:</td>
                    <td>--</td>
                  </tr>
                  <tr>
                    <td class="pr-1">Address:</td>
                    <td>${getDetail.address}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="col-6 p-0">
              <div class="float-right">
                <table>
                  <tbody>
                    <tr>
                      <td class="pr-1">Mobile No:</td>
                      <td>${getDetail.supplierNumber}</td>
                    </tr>
                    <tr>
                      <td class="pr-1">City:</td>
                      <td>${getDetail.city}</td>
                    </tr>
                    <tr>
                      <td class="pr-1">Email:</td>
                      <td>${getDetail.email}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th class="py-1">Item</th>
                <th class="py-1">Image</th>
                <th class="py-1">Article</th>
                <th class="py-1">Color</th>
                <th class="py-1">UOM</th>
                <th class="py-1">Qty</th>
                <th class="py-1">Rate</th>
                <th class="py-1">Amount</th>
              </tr>
            </thead>
            <tbody>
            ${getDetail.details.map((element) => {
        totalAmount += element.itemAmount;
        return `<tr class="border-bottom">
                  <td class="py-1">${element.itemMaster}</td>
                  <td class="py-0 px-25"><img src="assets/images/grn/${element.itemImage}" height="70px" width="100px"/></td>
                  <td class="py-1">${element.itemArticle}</td>
                  <td class="py-1">${element.itemColor}</td>
                  <td class="py-1">${element.itemUOM}</td>
                  <td class="py-1">${element.itemQuantity}</td>
                  <td class="py-1">${element.itemRate}</td>
                  <td class="py-1">${element.itemAmount}</td>
              </tr>`;
      })}
              <tr class="border-bottom border-top bg-light">
                <td colspan="5"></td>
                <td colspan="2"><span class="font-weight-bold">Total:</span></td>
                <td colspan="1"><span class="font-weight-bold">${totalAmount}</span></td>
              </tr>
              <tr class="border-bottom border-top bg-light">
                <td colspan="5"></td>
                <td colspan="2"><span class="font-weight-bold">Shipment Cost:</span></td>
                <td colspan="1"><span class="font-weight-bold">${getDetail.totalShipmentCost}</span></td>
              </tr>
              <tr class="border-bottom border-top bg-light">
                <td colspan="5"></td>
                <td colspan="2"><span class="font-weight-bold">Misc Cost:</span></td>
                <td colspan="1"><span class="font-weight-bold">${getDetail.totalMiscCost}</span></td>
              </tr>
              <tr class="border-bottom border-top bg-light">
                <td colspan="5"></td>
                <td colspan="2"><span class="font-weight-bold">Total GRN Cost:</span></td>
                <td colspan="1"><span class="font-weight-bold">${getDetail.totalCost}</span></td>
              </tr>
              <tr class="border-bottom border-top ">
                <td colspan="2">
                  <span class="font-weight-bold">Amount in Words:</span>
                </td>
                <td colspan="4"> <span class="font-weight-bold">${numberToWords(
        totalAmount
      )}</span></td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="card-body invoice-padding pt-0">
          <div class="row">
            <div class="col-12" style="height: 100px">
              <span class="font-weight-bold">Note: </span>
              <span
                >${getDetail.note}</span
              >
            </div>
          </div>
        </div>
      </div>`;
    this._reportView.getPdfContent.next(makePdf);
  }
}
