import { Component, OnInit } from '@angular/core';
import { SweetalertComponent } from 'app/shared/sweetalert/sweetalert.component';
import { GRNService } from '../../service/grn.service';
import { Router } from '@angular/router';
import { ReportService } from 'app/modules/reports/services/report.service';
import { goodsReceivingNoteReport } from 'app/shared/reports/goodsReceivingNote.service';
import { deliveryNoteReport } from 'app/shared/reports/deliveryNote.service';

@Component({
  selector: 'app-list-grn',
  templateUrl: './list-grn.component.html',
  styleUrls: ['./list-grn.component.scss']
})
export class ListGrnComponent implements OnInit {
  public contentHeader: Object;
  public tableDataArray = [];

  constructor(
    private _sw: SweetalertComponent,
    private _serviceData: GRNService,
    private _router: Router,
    private pdfService: goodsReceivingNoteReport,
    private deliveryNoteReport: deliveryNoteReport,
    private reportService: ReportService,
  ) { }

  ngOnInit(): void {
    this.getAllDetail();


    this.contentHeader = {
      headerTitle: "GRN",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Good Receiving Note",
            isLink: true,
            link: "/",
          },
          {
            name: "Entry",
            isLink: false,
          },
        ],
      },
    };
  }


  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      }else if(!res.data && this.tableDataArray.length == 1){
        this.tableDataArray= []
      }
    });
  }

  confirmDelete(id) {
    let deleteId = {
      id: id,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "GRN Records", "There is a problem");
        }
      )
    );
  }


  searchTerm: string = '';
 get filterTableData() {
    if (!this.searchTerm) {
      return this.tableDataArray;
    }
    return this.tableDataArray.filter(item => item.name.toLowerCase().includes(this.searchTerm.toLowerCase()));
  }

  makeReport(itemId){
    this.reportService.goodsReceivingNoteReport({orderId:itemId}).subscribe((getDetail) => {
      if(getDetail.data){
        this.pdfService.generatePdfContent(getDetail.data)
          this._router.navigate(['/reports/view']);        
      }
    });
  }

  deliveryNote(invoiceId){
    this.reportService.goodsReceivingNoteReport({orderId:invoiceId}).subscribe((getDetail) => {
      if(getDetail.data){
        this.deliveryNoteReport.generatePdfContent(getDetail.data)
          this._router.navigate(['/reports/view']);        
      }
    });
  }
}
